.appp {
  display: inline-flex;
  flex-direction: column;
  align-items: center;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  background-color: white;
  color: black;
  gap: 20px;
  border-radius: 10px;
  padding: 30px;
  margin: auto;
  width: 400px;
  margin-top: 50px;
}

.heading {
  font-size: 25px;
}
.options {
  list-style: none;
  padding: 0;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 20px;
}
.option {
  background-color: #0d0f11;
  color: #ebe2e2;
  padding: 10px;
  border: 1px;
  border-radius: 5px;
  cursor: pointer;
  text-align: center;
  transition: background-color 0.3s;
}
.next {
  margin: auto;
  width: 200px;
  height: 50px;
  background: #0e9684;
  color: white;
  font-size: 15px;
  outline-color: white;
  cursor: pointer;
  display: inline-block;
  border-radius: 10px;
}
.title {
  font-size: 35px;
}
.correct {
  background-color: green;
}
.incorrect {
  background-color: red;
}
.new {
  margin: auto;
  width: 200px;
  height: 50px;
  background: #0e9684;
  color: white;
  font-size: 15px;
  outline-color: white;
  cursor: pointer;
  display: inline-block;
  border-radius: 10px;
  margin-top: 10px;
  margin-left: 100px;
  gap: 10px;
}
.headig {
  font-size: 35px;
  margin-bottom: 2cm;
}
